<template>
  <el-container>
    <el-main>
      <div class="center">
        <!-- 步骤条 -->
        <div v-show="active === 0" style="font-size:12px;text-align:left">
          在每题的四个选项中只选择一个最符合你自己的，并在英文字母后面做记号。一共40题。不能遗漏。 注意：请按第一印象最快的选择，如果不能确定，可回忆童年时的情况，或者以你最熟悉的人对你的评价来从中选择。
        </div>
        <div v-for="(d, index) in mydata" class="testBox" :key="index" v-show="showTest">
          <!-- 里面的内容卡 -->

          <div>
            <div v-if="active == index" class="transition-box">
              <el-row>
                <div class="title">第{{ index + 1 }}题</div>
              </el-row>

              <el-radio-group v-model="checked[index]" @change="source(index)">
                <div class="myoption" v-for="(o, i) in d.ques" :key="i">
                  <el-row class="ostyle">
                    <el-radio :label="i">
                      <span class="txt"> {{ o.tit }}</span>
                    </el-radio>
                  </el-row>
                </div>
              </el-radio-group>
            </div>
          </div>

          <!-- 里面的内容卡 -->
        </div>
        <!-- 步骤条 -->
        <!-- 按钮组 -->
        <div class="btn" v-show="showTest">
          <el-button-group>
            <el-button type="primary" icon="el-icon-arrow-left" @click="pre" v-if="active > 0">上一题</el-button>
            <el-button type="primary" @click="next" v-if="active < len - 1">下一题<i class="el-icon-arrow-right el-icon--right"></i></el-button>
            <el-button type="primary" icon="el-icon-edit" v-if="active == len - 1" @click="findMost()">查看结果</el-button>
          </el-button-group>
        </div>
        <!-- 按钮组 -->
        <!-- 生成结果 -->
        <div class="proBox" v-show="showPros">
          <el-progress :text-inside="true" :stroke-width="26" :percentage="percentage"></el-progress>
          <div style="font-size:16px;margin-top:20px">
            测试结果生成中，请稍候
          </div>
        </div>
        <div class="resBox" v-show="showRes">
          <h2>测试结果为</h2>
          <div class="t_blod">{{ res.jianjie }}</div>
          <div>
            <span class="t_blod">代表人物：{{ res.daibiao }}</span>
          </div>
          <div class="t_blod">建议职业：{{ res.zhiye }}</div>
          <div><span class="t_blod">性格素描：</span>{{ res.sumiao }}</div>
          <div><span class="t_blod">特点：</span>{{ res.tedian }}</div>
          <div><span class="t_blod">缺点：</span>{{ res.quedian }}</div>
          <div><span class="t_blod">切入点：</span>{{ res.qieru }}</div>
          <div><span class="t_blod">注意点：</span>{{ res.zhuyidian }}</div>
          <div><span class="t_blod">自我规划:</span></div>
          <div v-for="(d, index) in res.guihua" :key="index + 10">
            <span>{{ d }}</span>
          </div>
          <div><span class="t_blod">情绪：</span>{{ res.qinxu }}</div>
          <div><span class="t_blod">恐惧：</span>{{ res.kongju }}</div>
          <div><span class="t_blod">作风:</span></div>
          <div v-for="(d, index) in res.zuofeng" :key="index + 2">
            <div>{{ d }}</div>
          </div>
          <div><span class="t_blod">目标：</span>{{ res.mubiao }}</div>
          <div><span class="t_blod">说明：</span>{{ res.shuoming }}</div>
          <div>
            <h2>和D、I、S、C性格的沟通技巧</h2>
            <h3>1、如何与D（力量）型沟通</h3>
            战略、目标、解决办法、行动计划之类的宏观话题对他们有吸引力<br />
            语速同频<br />
            试着划分职权范围和独立王国<br />
            说结果，提供三个以上方案供其选择<br />
            沟通时注重效率与业绩成果，指出你的建议将如何帮助他达成目标<br />
            不要辩护、争吵、直接反驳、使用结论性语言、啰啰嗦嗦<br />

            <h3>2、如何与I（活泼）型沟通</h3>
            表达尽量口语化，灵活、幽默，创造轻松的氛围<br />
            少说多听，给他们时间说话，并对其给予关注和兴趣<br />
            回应时要充满活力和热情，对他们的积极表现要多使用个性化赞扬<br />
            注意自己要明确目的，讲话直率<br />
            重要的事情，以书面形式与其确认，因为他们有可能说到但做不到<br />
            他们讲话可能时常跑题，在打断前对他们的好的说法加以肯定，如：“您说的这一点确实十分关键，正好引入咱们下一个话题。”<br />
            尽可能较少打断（插话）和反对<br />

            <h3>3、如何与S（和平）型沟通</h3>
            力求创造友谊的环境氛围，减少他们的戒心<br />
            讲话时放慢语速，面带微笑，亲切友好，避免过于正式的方式<br />
            亲情、友情方面等有关人的话题对他们有吸引力，所以讨论问题时要涉及到人的因素<br />
            提供个人帮助，建立信任关系<br />
            鼓励他们多发表看法，避免批评、挑战或催促<br />
            避免跟着此人的思路走<br />

            <h3>如何与C(完美)型沟通</h3>
            因为内向，所以一开始接触时不要过于友好和随便<br />
            他们喜欢按程序办事，所以沟通前最好有书面通知或者提前预约，并将议题罗列出来<br />
            他们愿意听事实，所以沟通前需准备充分，备好书面资料，并配以数据、图表等<br />
            准时到达，穿着整齐正式<br />
            语速不要太快，给他们提问和考虑的时间<br />
            讲明事情的“理由”，并保证信息的正确性<br />
            他们喜欢书面沟通，并配以事实、数据、图表、符号、附件说明等<br />
            沟通前最好给他们准备的时间，他们不喜欢仓促行事<br />
            激励D、I、S、C性格的方法<br />

            <h2>激励D、I、S、C性格的方法</h2>
            <h3>1、如何激励D（力量）型</h3>
            时刻支持他们的目标,赞扬他们的效率。<br />
            多口头鼓励及赞扬。<br />
            让他们在工作中自己弥补不足，而不要指责他们。<br />
            让他们自己先找出完成工作及解决问题的方法。<br />
            在合适的情况下，给更多的自主权。<br />
            在合适的情况下，让他辅助管理。<br />
            鼓励他与大家分享经验。<br />
            赞美他们的自信与果敢。<br />

            <h3>2、如何激励I（活泼）型</h3>
            对他们的生活表示关心，让他们感受到重视和尊重。<br />
            与他们的谈话时要注意沟通技巧，使他们觉得受到了尊重。<br />
            由于他们比较缺乏责任心，所以应承诺为他们担负一定责任。<br />
            给他们提供安全感。<br />
            给他们提供充分地和他人分享感受的机会。<br />
            避免让他们感觉到拒绝，否则他们会因此而不安。<br />
            赞美他们的影响力。<br />
            安排工作时，强调工作完成对于团队和成员的重要性，指明不完成工作对他人的影响。<br />

            <h3>3、如何激励S（和平）型</h3>
            支持他们的工作，对于他们的认真态度和敬业精神给予肯定和鼓励。<br />
            奖励他们的勤勉。<br />
            多给他们出主意、想办法。<br />
            不要随意换岗，必要时多给简单的工作<br />
            表扬他们的稳重<br />
            表扬他们值得信赖<br />
            表扬他们处变不惊<br />

            <h3>4、如何激励C(完美)型</h3>
            肯定他们的能力，尽量给予更多的鼓励。<br />
            称赞他们的工作表现，而不是夸奖他们工作的结果。<br />
            不是压榨而是人性化的关怀<br />
            先抑后扬的让他们改正自己的失误<br />
            公平的环境更适合激发他们的工作效率<br />

          </div>
        </div>
      </div>
    </el-main>
  </el-container>
</template>

<script>
import axios from 'axios'
export default {
  name: 'test',
  mounted: function () {
    this.queryones()
    this.getResData()
  },
  data () {
    return {
      mydata: [],
      checked: [],
      title: '',
      radio: '',
      len: 0,
      sum: 0,
      active: 0,
      showTest: true,
      percentage: 10,
      showPros: false,
      showRes: false,
      res: '',
      resData: ''
    }
  },
  methods: {
    // 判断是否登录
    checkLogin () {

    },
    queryones () {
      // 获取本地题目数据
      axios
        .get('test.json')
        .then(result => {
          this.mydata = result.data.data.questions
          this.len = this.mydata.length
        })
        .catch(function (err) {
          console.log(err)
        })
    },
    next () {
      // 下一题
      if (this.active++ > this.len - 1) {
        this.active = this.len - 1
      }
    },
    // 上一题
    pre () {
      if (this.active-- < 0) {
        this.active = 0
      }
    },
    source (i) {
      var count = 0
      var all = 0
      for (var j = 0; j < this.len; j++) {
        count = this.checked[j]
        if (count === undefined) {
          count = 0
        }
        all = Number(all) + Number(count)
      }
      // 赋值
      this.sum = all
    },
    // 找到最多选项 确认性格
    findMost (arr) {
      this.showTest = false
      this.showPros = true
      window.setInterval(() => {
        this.percentage++
        if (this.percentage > 100) {
          this.percentage = 100
          this.showRes = true
          this.showPros = false
        }
      }, 50)
      if (!this.checked.length) return
      if (!this.checked.length === 1) return 1
      const res = {}
      let maxName
      let maxNum = 0
      // 遍历数组
      !this.checked.forEach(item => {
        res[item] ? (res[item] += 1) : (res[item] = 1)
        if (res[item] > maxNum) {
          maxName = item
          maxNum = res[item]
        }
      })
      console.log(this.resData[maxName])
      this.res = this.resData[maxName]
    },
    // 获取结果
    getResData () {
      axios
        .get('resoult.json')
        .then(result => {
          this.resData = result.data.content
        })
        .catch(function (err) {
          console.log(err)
        })
    }
  }
}
</script>
<style>
body,
html,
#app,
.el-container{
  height: 98%;
  position: relative;
}
.testBox {
  margin-top: 20%;
  }
.proBox {
  margin-top: 200px;
}
.title {
  font-size: 28px;
  padding-bottom: 20px;
}
.btn {
  width: 220px;
  position:relative;
  left: 50%;
  transform: translateX(-50%);
  margin-top: 20px;
}
.txt {
  font-size: 12px;
  display: inline-block;
}
.myoption {
  word-wrap: break-word;
  word-break: break-all;
  max-width: 375px;
}
.ostyle {
  margin-top: 5%;
  text-align: left;
}
.transition-box {
  text-align: center;
  box-sizing: border-box;
  position: relative;
  /* left: 50%;
  transform: translateX(-50%); */
}
.t_blod {
  font-weight: bold;
}
.resBox {
  font-size: 12px;
  text-align: left;
}
</style>
